$prefix: '.rise-blocks';
#{$prefix}-counter{
    #{$prefix}-counter-number{
        input{
            border:none;
            text-align: center;
            max-width: 100%;
            height: initial;
            padding: 0;
            background-color: transparent;

            &:focus{
                outline: none;
                box-shadow: none;
            }

            &::-webkit-inner-spin-button,
            &::-webkit-outer-spin-button { 
                -webkit-appearance: none; 
                margin: 0; 
              }
        }
    }
}